.home {
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;

}
.home__content {
  width: 80%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home__sidebar {
  width: 20%;
  height: 90vh;
  background-color: white;
  box-shadow: 0 3em rgba(0, 0, 0, 0.1), 0 0 0 2px rgb(255, 255, 255),
    0.3em 1em 1em rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}
